<template>
  <div id="app">
    <nav-bar></nav-bar>
    <div class="container">
      <input v-on:keypress="_onSendToken" v-model="token" class="form-control" placeholder="Your token" />
      {{token}}
    </div>
  </div>

</template>

<script>
import NavBar from './components/NavBar';

export default {
  name: 'app',
  data() {
    return {
      ZEIT_ENDPOINT: 'https://api.zeit.co/now/deployments',
      token: '',
    };
  },
  methods: {

    fetchDeployments(token) {
      fetch(this.ZEIT_ENDPOINT, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
      })
        .then(res => res)
        .catch(err => err);
    },
    _onSendToken(evt) {
      if (evt.keyCode === 13) {
        this.fetchDeployments(this.token);
        return true;
      }
      return true;
    },
  },
  components: {
    NavBar,
  },
};
</script>

<style>
</style>



